<template>
  <view class="detail">
    <sun-header :title="$t('TransactionsDetails')"></sun-header>
    <sunmiModal v-if="bluetoothStatus && is_shangmi == 0"></sunmiModal>
    <view class="top">
      <view class="icon" :style="'width:'+iconW+'px;height:'+iconW+'px'">
        <image v-if="item.paymentStatus == 'success' && !item.refundStatus" src="/static/images/status/success.svg"
          mode="widthFix"></image>
        <image v-if="item.paymentStatus == 'success' && item.refundStatus" src="/static/images/status/back.svg"
          mode="widthFix"></image>
        <image v-if="item.paymentStatus == 'fail'" src="/static/images/status/fail.svg" mode="widthFix"></image>
        <image v-if="item.paymentStatus == 'pending' || item.paymentStatus == 'PENDING'"
          src="/static/images/status/wait.svg" mode="widthFix"></image>
      </view>
      <view class="text" v-if="status == 1"> ${{ item.amount / 100 }} </view>
    </view>
    <view class="card" :style="'padding:'+cPading+'px 13px'">
      <view class="lable">{{ $t('DateProcessed') }}</view>
      <view class="value">{{ item.lastUpdateTime }}</view>
    </view>
    <view class="card" :style="'padding:'+cPading+'px 13px'">
      <view class="lable">{{ $t('PaymentChannel') }}</view>
      <view class="value">{{ item.acquirerTypeAbbreviation }}</view>
    </view>
    <view class="bottom" :style="'padding:'+cPading+'px 13px'">
      <view class="title">
        {{ $t('TranscationID') }}
      </view>
      <view class="name">
        {{ item.id }}
      </view>
    </view>
    <view v-if="item.paymentStatus == 'success' && !item.refundStatus" class="button"
      style="background: #e34d59; margin-top: 30rpx" @click="issueRefund">
      <image src="/static/images/status/rufund.svg" mode="widthFix"></image>
      {{ $t('IssueRefund') }}
    </view>
    <view v-if="item.paymentStatus == 'success' && !item.refundStatus" class="button" @click="printCurrent">
      <image src="/static/images/status/printer.svg" mode="widthFix"></image>
      {{ $t('PrintReceipt') }}
    </view>
    <footer-logo></footer-logo>

    <view class="dialog" v-if="showInputPwd">
      <view class="mask"></view>
      <view class="modal">
        <view class="d-top">
          <view class="d-title">{{$t('PasswordIsRequired')}}</view>
          <input class="d-input" @input="getVal" type="safe-password" focus placeholder="" />
        </view>
        <view class="d-bottom">
          <view class="btn" @click="showInputPwd = false" style="color: rgb(0, 0, 0)">
            {{$t('Cancel')}}
          </view>
          <view class="btn" @click="submit" style="color: rgb(227, 77, 89)">
            {{$t('Refund')}}
          </view>
        </view>
      </view>
    </view>

  </view>
</template>

<script>
  import sunHeader from '@/components/sun-header/sun-header.vue'
  import sunmiModal from '@/components/sunmiModal/sunmiModal.vue'
  import api from '@/utils/api/index.js'
  import utils from '@/utils/util.js'
  import print from '@/utils/mixins/print.js'
  import _ from 'loadsh'
  export default {
    name: 'status',
    components: {
      sunHeader,
      sunmiModal
    },
    mixins: [print],
    data() {
      return {
        item: {},
        bluetoothStatus: false,
        currentItem: {},
        showInputPwd: false,
        password: '',
        status: 1, // 1 成功 2失败
        iconW: 100,
        cPading: 90,
		is_shangmi:0,
      }
    },
    onShow() {
      uni.getSystemInfo({
        success: (res) => {
          let h = res.windowHeight
          if (h < 580) {
            this.iconW = 80
            this.cPading = 0
          } else if (h >= 580 && h < 620) {
            this.cPading = 5
            this.iconW = 90
          } else {
            this.cPading = 10
            this.iconW = 100
          }
		  
		  if (res.deviceBrand == 'sunmi') {
		  	this.is_shangmi = 1
		    } else {
		  	this.is_shangmi = 0
		    }
          // this.winH = res.windowHeight
        }
      });
    },
    onLoad(e) {
      console.log(e, '-=-=')
      let JsonBigstring = require('json-bigint')({
        storeAsString: true
      });
      let resData = JsonBigstring.parse(e.item)
      this.item = resData
    },
    methods: {
      // 判断是否开启蓝牙
      judgeBluetooth() {
        // 主机模式
        return new Promise((resolve, reject) => {
          // mode: 'central',
          uni.openBluetoothAdapter({
            success: (r) => {
              console.log('蓝牙初始化成功')
              // 获取蓝牙的匹配状态
              uni.getBluetoothAdapterState({
                success: function(row) {
                  console.log('蓝牙状态：', row.available)
                  if (row.available) {
                    resolve()
                  } else {
                    // 请开启蓝牙
                    this.remindOpenBluetooth()
                    reject()
                  }
                },
                fail: function(err) {
                  // 请开启蓝牙
                  this.remindOpenBluetooth()
                  reject()
                },
              })
            },
            fail: (err) => {
              // 请开启蓝牙
              console.log('蓝牙初始化失败' + JSON.stringify(err))
              this.remindOpenBluetooth()
              reject()
            },
          })
        })
      },
      // 提示用户需要开启蓝牙
      remindOpenBluetooth() {
        this.bluetoothStatus = true
        uni.onBluetoothAdapterStateChange((blu) => {
          if (blu.available) {
            this.bluetoothStatus = false
          }
        })
      },

      issueRefund() {
        this.showInputPwd = true
      },
      async submit() {
        if (!this.password) {
          uni.showToast({
            icon: 'none',
            title: this.$t('PasswordIsRequired')
          })
          return
        }
        let o = {
          voidPassword: this.password
        }
        utils.showLoading()

        let res = await api.validateRefundPassword(o)
        uni.hideLoading()
        if (res.code == '000000') {
          this.returnPayment()
        } else {
          utils.showText(res.message)
        }
      },
      // 发起退款
      async returnPayment() {
        utils.showLoading()
        let o = {
          acquirerType: this.item.acquirerType,
          paymentTerminalRequestVo: {
            transactionId: this.item.id + '',
            amount: this.item.amount,
            actionType: 'REFUND'
          }
        }
        let res = await api.paymentPay(o)
        uni.hideLoading()
        if (res.code == '000000') {
          utils.showSuccess('Success')
          setTimeout(() => {
            uni.navigateBack({
              delta: 1
            })
          }, 1000)
        } else {
          utils.showText(res.message)
        }
      },
      getVal(e) {
        console.log(e)
        this.password = e.detail.value
      },
      printCurrent() {
        this.judgeBluetooth().then(() => {
          let item = _.cloneDeep(this.item)
          console.log(item, '12312')
          item.acquirerType = item.acquirerTypeAbbreviation
          uni.getSystemInfo({
            success: (res) => {
              if (res.deviceBrand == 'sunmi') {
                this.printSunmi(item, 1, true)
              } else {
                this.printText(item, 1, true)
                // uni.navigateTo({
                // 	url:'/pages/web/web?src=https://mp.imin.sg/WebPrint/M.html'
                // })
              }
            }
          })
        })

      },
      back() {
        uni.navigateBack()
      },
    },
  }
</script>

<style lang="scss" scoped>
  @import 'detail.scss';

  .uni-modal__textarea {
    height: 70rpx;
    text-align: left;
    width: 100%;
    box-sizing: border-box;
    padding-left: 56rpx;
    background: transparent !important;
    margin-bottom: 20rpx;
    font-size: 40rpx;
  }
</style>